/* eslint-disable jsx-a11y/label-has-associated-control */
/* eslint-disable jsx-a11y/label-has-for */
import React, { PureComponent } from 'react';
import {
  CardNumberElement,
  CardExpiryElement,
  CardCVCElement
} from 'react-stripe-elements';
import '../../css/Stripe.css';

class CardSection extends PureComponent {
  render() {
    return (
      <label>
        <span className="label">Card details</span>
        <CardNumberElement id="cardnumber" className="input element" />
        <CardExpiryElement id="exp-date" className="input element-half-width" />
        <CardCVCElement className="input element-half-width" />
      </label>
    );
  }
}

export default CardSection;
