import React, { Component } from 'react';
import { Loading, Notification } from 'element-react';
import { injectStripe } from 'react-stripe-elements';
import { withStyles } from '@material-ui/core/styles';
import '../../css/Stripe.css';
import paymentLogo from '../../assets/payment-logo.png';
import securedLogo from '../../assets/secured.jpg';
import creditcard from '../../assets/credit-cards.png';
import CardSection from './CardSection';

import styles from '../../styles/CheckoutForm.css';
import { UserContext } from '../../contexts/UserContext';

class CheckoutForm extends Component {
  static contextType = UserContext;

  state = {
    fullscreen: false
  };

  handleSubmit = async ev => {
    this.setState({
      fullscreen: true
    });

    // We don't want to let default form submission happen here, which would refresh the page.
    ev.preventDefault();

    const user = this.context.user;
    const stripeData = await this.props.stripe.createToken({
      name: `${user.firstname} ${user.lastname}`
    });
    const skuId = this.props.sku;

    try {
      this.context.buyNewProduct(stripeData.token, skuId);

      Notification({
        title: 'Success',
        message: 'Order successful',
        type: 'success'
      });
    } catch (message) {
      Notification.error({
        title: 'Error',
        message: 'There was a problem processing your order'
      });
    }

    this.props.redirect();
  };

  render() {
    const { classes } = this.props;

    return (
      <form onSubmit={this.handleSubmit} className={classes.form}>
        <img src={securedLogo} alt="secured" className={classes.securedLogo} />
        <CardSection />
        <button type="submit" className={classes.paymentButton}>
          Confirm order
        </button>
        <div className={classes.paymentContainer}>
          <img
            className={classes.imageCreditCard}
            alt="credit card payment"
            src={creditcard}
          />
          <p className={classes.stripeSecuredTextLine}>secured with</p>
          <a
            href="https://stripe.com"
            target="_blank"
            rel="noopener noreferrer"
          >
            <img
              className={classes.paymentLogo}
              src={paymentLogo}
              alt="Stripe Payments"
            />
          </a>
        </div>
        {this.state.fullscreen && <Loading text="Chargement..." fullscreen />}
      </form>
    );
  }
}

export default withStyles(styles)(injectStripe(CheckoutForm));
